<template>
  <div class="content">
    <div class="banaer">
      <div class="container">
        <div class="userCenterBar">
          <el-tabs tab-position="left">
            <el-tab-pane label="个人中心">
              <Info :user="User"></Info>
            </el-tab-pane>
            <el-tab-pane label="成为作者" v-if="User.type<2">
              <Permit></Permit>
            </el-tab-pane>
            <el-tab-pane label="账户安全">
              <Repwd></Repwd>
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Info from 'view/user/base/user'
import Repwd from 'view/user/base/repwd'
import Permit from 'view/user/base/permit'
export default {
   data() {
      return {
        User:{}
      }
    },
    methods: {
      
    },
    components: {
      Info,
      Repwd,
      Permit
    },
    created(){
      this.User = JSON.parse(sessionStorage.getItem('u'))
    }
}
</script>

<style lang="stylus" rle="stylesheet/stylus">
.el-tabs__item:hover
  color red
.el-tabs__item.is-active
  color #FF4040
.el-tabs__active-bar
  background-color #FFB5C5
.banaer
  padding-top 10px
  // background url(../images/new/m_banner_bak1.png)
  background-position top center
  background-repeat no-repeat
  min-width 1000px
  .container
    width 1000px
    margin 0 auto
    overflow hidden
    position relative
    .userCenterBar
      background-color #fff
      margin 20px 0
</style>



